* {
  color: #0b051f;
}

body {
  height: 100vh;
  width: 100%;
  position: relative;
  background-image: linear-gradient(290deg, #fdfbfb 0%, #ebedee 100%);
}

.App {
  font-family: "SF Pro Text";
  text-align: center;
  display: flex;
  flex: 1 1 auto;
  margin: 1em;
  flex-flow: column nowrap;
  min-height: 200px;
  height: 100%;
  position: relative;
  align-content: center;
  height: 100%;
}

form {
  display: flex;
  flex-flow: column nowrap;
  width: 100%;
  position: relative;
  flex: 1 1 auto;
  justify-content: space-between;
  align-self: space-around;
}

label {
  font-size: 15px;
  letter-spacing: 1.4px;
  font-weight: 600;
  text-transform: uppercase;
  width: 90%;
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  align-self: center;
  padding: 12px;
  color: #44484e;
}

button {
  padding: 1.1em;
  border-radius: 4px;
  font-size: 13px;
  letter-spacing: 1.4px;
  font-weight: 600;
  text-transform: uppercase;
  max-width: 50%;
  align-self: center;
  margin: 12px 0px;
  /* border: none; */

  /* box-shadow: 3px 5px 5px #eeedfa; */
}

input {
  padding: 12px;
  margin: 3px 4px;
  border: none;
  height: 20px;
  /* box-shadow: 3px 5px 5px #eeedfa; */
  width: 370px;
  outline: none;
  border-top: transparent;
  border-left: transparent;
  border-right: transparent;
  border-bottom: 1px white solid;
  background: transparent;
  font-family: sans-serif;
  font-size: 18px;
}

button {
  background-color: white;
  border-color: #667eea;
  color: #667eea;
}

input:focus {
  /* override default browser outline */

  /* outline: 0;
  /* apply other styles */
  /* border-radius: 2px; */

  border-bottom-color: #667eea;
  border-radius: none;
}

/* input:hover, */
button:hover {
  transform: scale(1.05);
  transition: all 0.7s ease;
  background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

input[type="text"] {
  font-family: sans-serif;
  outline: 0;
  background: transparent;
}
